import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable()
export class AuthService {
  baseUrl = 'http://localhost:51190/api/auth/'

  constructor(private http: HttpClient) { }

  login(model) {
    const contentHeader = new HttpHeaders({ 'Content-type': 'application/json' });
    var credentials = { 'userName': model.username, 'password': model.password };
    return this.http.post(this.baseUrl + 'login', credentials, { headers: contentHeader });

  }

  register(model) {
    const contentHeader = new HttpHeaders({ 'Content-type': 'application/json' });
    var newUser = { 'userName': model.username, 'password': model.password };
    return this.http.post(this.baseUrl + 'register', newUser, { headers: contentHeader });
  }
}



